"use client";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { ReactLogo, NodeTree, Database, Server } from "lucide-react"; // Icons

const topics = [
  { name: "React", href: "/preparationstreet/react", icon: <ReactLogo className="w-5 h-5" /> },
  { name: "NodeJS", href: "/preparationstreet/nodejs", icon: <NodeTree className="w-5 h-5" /> },
  { name: "MongoDB", href: "/preparationstreet/mongodb", icon: <Database className="w-5 h-5" /> },
  { name: "ExpressJS", href: "/preparationstreet/expressjs", icon: <Server className="w-5 h-5" /> },
];

export default function Sidebar() {
  const pathname = usePathname();

  return (
    <aside className="w-64 bg-gradient-to-b from-gray-900 to-black text-white min-h-screen p-6 shadow-xl">
      <h2 className="text-2xl font-bold mb-10 text-center tracking-wide">
        PreparationStreet
      </h2>

      <ul className="space-y-3">
        {topics.map((topic) => {
          const isActive = pathname === topic.href;
          return (
            <li key={topic.name}>
              <Link
                href={topic.href}
                className={`
                  flex items-center gap-3 px-4 py-3 rounded-lg transition-all duration-300
                  ${isActive ? "bg-gradient-to-r from-purple-600 to-pink-500 text-white shadow-lg" : "hover:bg-gray-800 hover:shadow-md"}
                `}
              >
                {topic.icon}
                <span className="font-medium">{topic.name}</span>
              </Link>
            </li>
          );
        })}
      </ul>

      <div className="mt-auto text-center mt-10 text-gray-400 text-sm">
        &copy; 2025 PrepStreet
      </div>
    </aside>
  );
}
